      <canvas id='edit_canvas' width='100%' height='100%'></canvas>
    </div> -->
    <img ref="img" v-show="false" :src="imgUrl" alt="">
    <div class="edit_barTop" >
      <div class="edit-btn edit-btn1" v-on:click="isSave()">
        <!-- <input type="file" accept="image/*" @change="uploadImg($event)"> -->
      </div>
      <div class="edit-btn edit-btn4" @click="toCustomizedBox">
        <span class="num-icon">1</span>
      </div>
      <div class="edit-btn edit-btn5" @click="confirm">
        <!-- <span class="num-icon">1</span> -->
      </div>
    </div>

    <div class="mask1" v-show="isMask">
