            <div>{{item.name}}</div>
          </section>         
          <!-- <section @click="hideMask()">
            <img src="http://img1.imgtn.bdimg.com/it/u=594559231,2167829292&fm=27&gp=0.jpg" alt="">
            <div>正面</div>
          </section> -->
        </div>
      </div>
      
    </div>

    <!-- 手势插件start -->
    <div id="cnt">
       <div 
        v-finger:press-move="pressMove"
        v-finger:pinch="pinch"
          >
          <div id="a">
            <img ref="a" :src="uploadImgList" :style='[styleObj]' alt="">
          </div>     
      </div>
    </div>
    <!-- 手势插件end -->

    <!-- <div class="imgBox">
        <img ref="pressMoveImg" id="pressMoveImg" :src="uploadImgList" />
    </div> -->
    <div class="edit_bar">
      <div class="edit-btn edit-btn1">
        <input type="file" accept="image/*" @change="uploadImg($event)">
        <span class="num-icon">1</span>
      </div>
      <div class="edit-btn edit-btn2" @click="showBottomMask(1)"></div>
      <div class="edit-btn edit-btn3">
        <input id="img" type="file" accept="image/*" @change="uploadImg($event)" >
      </div>
      <div class="edit-btn edit-btn4" @click="showBottomMask(2)"></div>
      <div class="edit-btn edit-btn5" @click="showMaskLeft"></div>
    </div>
    <norms1 
					v-on:hideMask="hideMask"
					:showMask="showMask" 
					:specList="specList"
					:sizeList="sizeList"
					:skuList="skuList"
					:goodsInfo="goods"
          :flag="flag"
          :gongYiList="gongYiList"
					></norms1>
      <!--<norms 
					v-on:hideMask="hideMask" 
					:showMask="showMask" 
					:specList="specList"
					:sizeList="sizeList"
					:skuList="skuList"
					:goodsInfo="goods"
					v-if= "(specList.length > 0 || sizeList.length > 0) && goodsImmediately"></norms> -->
          
  </div>
</template>

<script>
import vueTransform from "css3transform/vue/transform.vue";
import Bscroll from "better-scroll";
import Norms1 from "@/components/Norms/Norms1";
import Norms from "@/components/Norms/Norms";
import index from "vue";

export default {
  data() {
    return {
      flag: true,
      type: 2,
      enterType: "",
      showMask: false,
      article: "",
      specList: [],
      sizeList: [],
      skuList: [],
      goods: {},
      goodsImmediately: "",

      gongYiList: [],

      Stage: "",
      uploadImgList: [],
      styleObj: {
        transform: ""
      },
      translateX: 0,
      translateY: 0,
      zoom: 0,
      scaleX: 1,
      scaleY: 1,
      isMask: true,

      customizedList: [],
      isActive: 0,
      skuList: [],
      imgUrl: "",
      PARAMS: {
        a: 1,
        id: 0,
        skuId: 1,
        spuId: 1,
        worksImageIds: "0",
        attrs: [
          {
            imageId: 1,
            placeId: 1,
            craftId: 1,
            area: {
              heigth: 1,
              width: 1,
              x: 1,
              y: 1
            }
          }
        ]
      },
      x:0,
      y:0,
      width:0,
      height:0,
      craftId:0,
      imageId:0,
      placeId:0,
      id:0,
    };
  },
  watch: {},
  created() {
    this.getCustomizedList();
    this.$Api.getDesignInfo(0,1).then(res=>{
      console.log(res.q.id)
      this.id = res.q.id
    })
    this.skuList = JSON.parse(localStorage.getItem("skuList")) || {};
      
  },
  mounted() {
    // this.$nextTick(() => {
    //   this.scroll = new Bscroll(this.$refs.wrapper, {});
    //   console.log(this.$refs.wrapper);
    //   this.scroll.refresh();
    // });
  },
  components: {
    Norms1,
    Norms,
  },
  methods: {
    paramsInit() {
      this.PARAMS = {
        a: '1',
        id: this.id,
        skuId: this.$route.query.skuId,
        spuId: this.$route.query.spuId,
        worksImageIds: '0',
        attrs: [
          {
            imageId:`${this.imageId}`,
            placeId: `${this.placeId}`,
            craftId: `${this.craftId}`,
            area: {
              heigth:`${this.height}`,
              width: `${this.width}`,
              x: `${this.x}`,
              y: `${this.y}`
            }
          }
        ]
      }
      JSON.stringify(this.PARAMS)
      console.log(this.PARAMS)
    },
    SubmitCustomized() {
      this.paramsInit()
      // Object.keys(this.PARAMS).forEach(
      //   key => this.PARAMS[key] == "" && delete this.PARAMS[key]
      // );

      this.$Api.SubmitCustomized(this.PARAMS).then(res => {
        // console.log(res)
        if(res.q.d =="操作成功"){
          this.$toast("加入定制蓝");
          this.id = res.q.id
        }else{
          this.$toast(res.q.d)
        }
      });
    },
    toCustomizedBox(){
    	this.$toast('建设中,敬请期待...');
    	return;
      this.$router.push({name:'CustomizedBox',query:{id :this.id}})      
    },
    isSave() {
      // console.log(good);
      // let goodInfo = good;
      this.$popup({
        content: "您的设计尚未保存，是否保存并自动加入设计篮",
        cancelText: "不保存",
        sureText: "保存"
      }).then(res => {
        if (res == "sure") {
          // console.log(goodInfo);
          // let params = {
          //   skuId: goodInfo.skuId,
          //   spuId: goodInfo.spuId,
          //   num: 0
          // };
          // this.getCartUpdate(2, params);
        } else {
          this.$router.back(-1);
        }
      });
    },
    confirm() {
    	this.$toast('建设中,敬请期待...');
    	return;
      this.SubmitCustomized();
    },
    hideMask(obj) {
      // console.log(item)
      obj.cancle == "cancle" ? (this.showMask = false) : (this.showMask = true);
      this.craftId = obj.gongYiID 
    },
    showBottomMask(flag) {
      this.showMask = true;
      flag === 1 ? (this.flag = true) : (this.flag = false);
      // this.enterType = 2;
    },
    hideMaskLeft(item, index) {
      // console.log(123);
      this.isMask = false;
      //样式属性
      this.isActive = index;
      // console.log(item);
      this.gongYiList = item.crafts;

      this.imageId = item.sampleImage.id
      this.placeId = item.placeId
      // console.log( this.gongYiList)
      document.querySelector("#customized-edit").style.background = `url(${
        item.sampleImage.imagePath
      }) top left/100% auto no-repeat `;

      this.imgUrl = item.sampleImage.imagePath;
    },
    showMaskLeft() {
      if (!this.isMask) {
        this.isMask = true;
      } else {
        this.isMask = false;
      }
    },
    getCustomizedList() {
      let skuId = this.$route.query.skuId;
      this.$Api.getCustomizedList(skuId).then(res => {
        console.log(res);
        if (res.q.s == 0) {
          //初始化定制数据
          this.customizedList = res.q.places;
          //初始化工艺数据
          this.gongYiList = this.customizedList[0].crafts;
          //初始化默认背景
          document.querySelector("#customized-edit").style.background = `url(${
            this.customizedList[0].sampleImage.imagePath
          }) top left/100% auto no-repeat`;
        }
      });
    },
    pinch: function(evt) {
      evt.preventDefault();
      let initScale = 1;
      this.scaleX = initScale * evt.zoom;
      this.scaleY = initScale * evt.zoom;
      this.styleObj.transform = `translate(${this.translateX}px,${
        this.translateY
      }px) scale(${this.scaleX},${this.scaleY})`;

    },
    pressMove: function(evt) {
      evt.preventDefault();
      // console.log(evt.deltaX);
      // console.log(evt.deltaY);
      // console.log("onPressMove");
      this.translateX += evt.deltaX;
      this.translateY += evt.deltaY;
      this.styleObj.transform = `translate(${this.translateX}px,${
        this.translateY
      }px) scale(${this.scaleX},${this.scaleY})`;

      this.getX();
      this.getY();
      this.getWidth();
      this.getHeight();
    },
    getWidth(){
      this.width = parseInt(this.$refs.img.naturalWidth /
        window.screen.width *
        this.$refs.a.width *
        this.scaleX)
        
    },
    getHeight(){
      this.height =parseInt(this.$refs.img.naturalWidth /
        window.screen.width *
        this.$refs.a.width *
        this.scaleX)
    },
    getX() {
      const X = {
        clientX: this.$refs.a.getBoundingClientRect().left,
        screenX: window.screen.width,
        imgX: this.$refs.img.naturalWidth
      };

      this.x = parseInt(X.clientX / X.screenX * X.imgX)
    },
    getY() {
      const Y = {
        clientY: this.$refs.a.getBoundingClientRect().top,
        screenY: window.screen.height,
        imgY: this.$refs.img.naturalHeight
      };

      this.y =parseInt(Y.clientY / Y.screenY * Y.imgY) 
      // console.log(y)
    },
    uploadImg($event) {
      // this.$Api.getDesignInfo().then(res=>{
      //   console.log(res)
      //   this.PARAMS.id = res.q.id

      // });
      var file = document.querySelector("#img").files[0];
      // console.log(file);

      var reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = oFREvent => {
        // console.log(oFREvent);
        // this.uploadImgList.push(oFREvent.currentTarget.result) ;
        this.uploadImgList = oFREvent.currentTarget.result;
      };
    },

    getIds(ids) {
      console.log(ids);
      this.imgIdList = ids;
    },
    // uploadImg($event) {
    //   var file = $event.target.files;
    //   //console.log(file[0])
    //   var URL = function(blob) {
    //     return window[window.webkitURL ? "webkitURL" : "URL"][
    //       "createObjectURL"
    //     ](blob);
    //   };
    //   var blob = URL(file[0]);
    //   console.log(blob);
    // },
    onRead(file) {
      console.log(file);
    }
    // addImg(src){
    //   var ld = new AlloyPaper.Loader();
    //   ld.loadRes([
    //     { id: "simg", src: src }
    //   ]);
    //   ld.complete(function () {
    //     var bmp = new AlloyPaper.Bitmap(ld.get("simg"));
    //     // console.log(bmp);
    //     bmp.originX = 0.5;
    //     bmp.originY = 0.5;
    //     bmp.x = stage.width / 2;
    //     bmp.y = stage.height / 2;
    //     bmp.scaleX =  bmp.scaleY = stage.width/bmp.width;

    //     stage.add(bmp);

    //     $(".addImgFrame").hide();
    //     stage.update();

    //     var initScale = 1;
    //     new AlloyFinger(bmp, {
    //       multipointStart: function () {
    //         initScale = bmp.scaleX;
    //       },
    //       rotate: function (evt) {
    //         bmp.rotation += evt.angle;
    //         stage.update();
    //       },
    //       pinch: function (evt) {
    //         bmp.scaleX = bmp.scaleY = initScale * evt.scale;
    //         stage.update();
    //       },
    //       pressMove: function (evt) {
    //         if(isCP){
    //           bmp.x += evt.deltaX*5;
    //           bmp.y += evt.deltaY*5;
    //         }else{
    //           bmp.x += evt.deltaX*3;
    //           bmp.y += evt.deltaY*3;
    //         }

    //         stage.update();
    //       },
    //       singleTap: function(evt){
    //         if(bmp.id != 1){
    //           delIcon(bmp);
    //         }
    //       }

    //     });
    //   });
    // }
  }
};
</script>

<style lang="scss">
@import "../../sass/baseSass.scss";
#customized-edit {
  height: 100%;
  width: 100%;
  // background-size: 100% auto;
  // background-origin:content-box;
  background: white;
  // overflow: hidden;
  #cnt {
    position: fixed;
    top: box(100);
    // z-index: -1000;
    #a {
      img {
        width: box(200);
        height: box(200);
        border: none;
      }
    }
  }

  .edit_bar {
    width: 100%;
    height: box(100);
    background: rgba(0, 0, 0, 0.5);
    position: fixed;
    bottom: 0;
    left: 0;

    .edit-btn {
      width: 20%;
      height: 100%;
      position: relative;
      float: left;
    }
    .edit-btn1 {
      background: url("../../assets/edit_uploadFile.png") no-repeat center
        center;
      background-size: auto 75%;
      input {
        width: 100%;
        height: 100%;
        filter: alpha(opacity=0);
        -moz-opacity: 0;
        -khtml-opacity: 0;
        opacity: 0;
      }
      .num-icon {
        position: absolute;
        font-size: 0.32rem;
        left: 1.2rem;
        top: 0.08rem;
        display: inline-block;
        min-width: 0.4rem;
        height: 0.4rem;
        border-radius: 50%;
        color: #fff;
        background: #fe4436;
        text-align: center;
      }
    }
    .edit-btn2 {
      background: url("../../assets/edit_param.png") no-repeat center center;
      background-size: auto 75%;
    }
    .edit-btn3 {
      background: url("../../assets/edit_img.png") no-repeat center center;
      background-size: auto 75%;
      input {
        width: 100%;
        height: 100%;
        filter: alpha(opacity=0);
        -moz-opacity: 0;
        -khtml-opacity: 0;
        opacity: 0;
      }
    }
    .edit-btn4 {
      background: url("../../assets/edit_type.png") no-repeat center center;
      background-size: auto 75%;
    }
    .edit-btn5 {
      background: url("../../assets/edit_dir.png") no-repeat center center;
      background-size: auto 75%;
    }
  }
  .edit_barTop {
    width: 100%;
    height: box(100);
    background: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;

    .edit-btn {
      width: 20%;
      height: 100%;
      position: relative;
      float: left;
    }
    .edit-btn1 {
      background: url("../../assets/common_btn_arrow2@2x.png") no-repeat center
        center;
      background-size: auto 75%;
    }
    .edit-btn4 {
      background: url("../../assets/home_btn_basket2@2x.png") no-repeat center
        center;
      background-size: auto 75%;
      float: right;
      .num-icon {
        position: absolute;
        font-size: 0.32rem;
        left: 1.2rem;
        top: 0.08rem;
        display: inline-block;
        min-width: 0.4rem;
        height: 0.4rem;
        border-radius: 50%;
        color: #fff;
        background: #fe4436;
        text-align: center;
      }
    }
    .edit-btn5 {
      background: url("../../assets/home_btn_ok@2x.png") no-repeat center center;
      background-size: auto 75%;
      float: right;
      position: absolute;
      right: box(90);
    }
  }
  .mask1 {
    position: fixed;
    right: 0;
    top: 0;
    width: box(300);
    height: 100%;
    padding: 0 box(30);
    background-color: rgba(0, 0, 0, 0.8);
    overflow: auto;
    header {
      text-align: center;
      font-size: box(36);
      color: white;
      height: box(100);
      line-height: box(100);
    }
    .wrapper {
      height: 100%;
      section {
        height: box(428);
        width: box(240);
        margin-bottom: box(20);
        // background-color: red;
        position: relative;
        // &:nth-child(1){
        //   background: url('../../assets/common_btn_arrow2@2x.png') no-repeat center center;
        // }
        // &:nth-child(1){
        //   border: solid box(6) #da36f7;
        // }
        &.active {
          border: solid box(6) #da36f7;
        }
        img {
          width: box(240);
          height: box(428);
        }
        div {
          height: box(76);
          width: 100%;
          position: absolute;
          bottom: 0;
          background-color: rgba(250, 250, 250, 0.5);
          text-align: center;
          line-height: box(76);
          font-size: box(32);
        }
      }
    }
  }
}
</style>



